.todo-list-item{
    list-style:none;
    margin:0px;
    padding:0px;
    margin:20px 0px;
}
.todo-list-item__label{
    padding:0px 20px;
}
.todo-list-item__label-chacbox{
    display:none;
}
.todo-list-item__label-fake-chacbox{
    position:relative;
    width:20px;
    height:20px;
    background:#fff;
    border:1px solid blue;
    border-radius:100%;
    cursor:pointer;
}
.todo-list-item__label-fake-chacbox::before{
    content:"";
    position:absolute;
    width:17px;
    height:17px;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    background:blue;
    border-radius:100%;   
    transition:all .3s;
    opacity:0;
}
.todo-list-item__label-chacbox:checked + .todo-list-item__label-fake-chacbox::before{
    opacity:1;
}
.todo-list-item{
    display:flex;
    align-items:center;
    justify-content:space-between;
}
.todo-list-item__text{
    flex:1;
    font-size:20px;
    color:#535151;
    padding:0px 15px;
}
.btn{
    padding:10px 20px;
    margin:0px 15px;
    cursor:pointer;
    border:none;
    font-size:15px;
    border-radius:20px;
}
.btn-important{
    background:#fbff00;
    color:#000;
}
.btn-delete{
    background:#ff0000;
    color:#fff;
}