.addTodo{
    display:flex;
    z-index:9999999999999999999;
}
.add-todo-input{
    width:600px;
    padding:30px 10px;
    font-size:20px;
    border-radius: 40px;
    margin-right:20px;
}
.add-todo-input::placeholder{
    font-size:20px;
}
.add-todo-btn{
    background:#000;
    color:#fff;
    font-size:20px;
    cursor:pointer;
    border-radius:20px;
    transition:all .3s;
    padding:0px 30px;
}
.add-todo-btn:hover{
    background:#fff;
    color:#000;
}