.btn-footer-wrapp{
    z-index:11;
    border-radius:50%;
    position:absolute;
    width:100px;
    background:#fff;
    display:flex;
    justify-content:center;
}
.btn-footer{
    top:-40px;
    left:50%;
    transform:translateX(-50%);
    border-radius:50%;
    position:absolute;
    width:80px;
    padding:10px 0px;
    background:#fff;
    display:flex;
    justify-content:center;
    border:none;
    background:#fff;
}
.btn-footer__img{
    width:50px;
    height:50px;
    border-radius:100%;
    background:#000;
    color:#fff;
    border:none;
    cursor:pointer;
}