.modal{
    position:fixed;
    left:0px;
    top:-1000%;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.7);
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    z-index:99999999999;
    transition:all .2s ease;
}
.modal.active{
    top:0px;
}
.btn-close{
    width:80px;
    height:80px;
    position:fixed;
    top:20px;
    right:100px;
    background:none;
    color:#fff;
    cursor:pointer;
}
.btn-close:hover{
    transform:rotate(90deg)
}