.nav{
    padding:20px 0px;
}
.nav__list{
    list-style:none;
    display:flex;
    margin:0px;
    align-items:center;
}
.nav__list-item{
    margin:0px 20px;
}
.nav__list-item-link{
    text-decoration:none;
    padding:10px;
    border-radius:20px;
    font-size:20px;
    background:#000;
    color:#fff;
    font-weight:800;
}
.nav__list-item-link.active{
    background:orange;
}